import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    sTitle: ''
  },
  getters: {
    // store의 상태값을 반환함
    // fnGetData함수가 콜링되면 업데이트된 state.sTitle 값을 읽어서 반환해준다
    fnGetData(state) {
      return state.sTitle;
    },
  },
  // mutations는 getters의 대칭되는 setters의 역할을 설정
  mutations: {
    // 매개변수로 전달받은 payload값을 store에 저장
    fnSetData(state, payload) {
      return state.sTitle = payload;
    }
  }
});
